

<template>
  <div>
      <h1 class="center">{{ title }}</h1>
      <p v-show="msg" class="center">{{ msg }}</p>
    
   
      <div class="row">
        <div class="input-field col s12 m6">
          <input id="icon_prefix" type="text"  @input="filter = $event.target.value" class="validate">
          <label for="icon_prefix">Search...</label>
        </div>
      </div>
   
   
   
      <ul class="row">
        
      <li class="pokemon" v-for="pokemon of searchPokemons">
          
          
          <board-container
                 :title="pokemon.name"
                 :url="pokemon.image"
                 :name="pokemon.name">
         
            
            <router-link :to="{ name: 'profile', params: { id : pokemon.id }}">
              <button class="btn red" name="profile">
                <i class="fa fa-book"></i>
                Profile
              </button>
            </router-link>  
            
            
          </board-container>
              
            



        </li> 
        
      </ul>
     
        
      <div class="center">
         <button class="load-button btn red " name="load" 
          @click="loadMorePokemons()">
          <i class="fa fa-spinner"></i>
          Load More
         </button>
      </div>
      
  </div>
</template>

<script>


import _ from 'lodash'
import Board from '../shared/board/Board.vue'
import ImageResponsive from '../shared/image-responsive/ImageResponsive.vue'


import PokemonService from '../../domain/pokemon/PokemonService'



export default {

  


  components:{
    'board-container':Board,
    'image-responsive':ImageResponsive
  },

  
  data () {
    return {
      title:"PokÃ©dex - HE:Lab's",
      pokemons:[],
      filter:'',
      msg:'',
      loading:'Loading...'
    }
  },
  

  created(){

    this.service = new PokemonService(this.$resource)
    

    this.service
      .listPokemons(this.pokemons.length, 9)
      .then(pokemons => {
        console.log(pokemons)
         this.pokemons = pokemons
      }, err => this.msg = err.message)

    

  },


  computed:{
    searchPokemons(){

      if(this.filter){
        let exp = new RegExp(this.filter.trim(), 'i')
        return this.pokemons.filter(pokemon => exp.test(pokemon.name))
      }else{
        
        return this.pokemons
      }


    }
    
  },

  methods:{

    loadMorePokemons(){
      this.service = new PokemonService(this.$resource)
      this.service
        .listPokemons(this.pokemons.length, 9)
        .then(pokemons => {
          this.pokemons = this.pokemons.concat(pokemons)
        }, err => this.msg = err.message)

      }


  }


 
}
</script>

<style>
  .load-button{
    margin-bottom:1em;
  }

   .input-field label {
     color: #000;
   }
   .input-field input[type=text]:focus + label {
     color: #000;
   }
   .input-field input[type=text]:focus {
     border-bottom: 1px solid #000;
     box-shadow: 0 1px 0 0 #000;
   }
   .input-field input[type=text].valid {
     border-bottom: 1px solid #000;
     box-shadow: 0 1px 0 0 #000;
   }
   .input-field input[type=text].invalid {
     border-bottom: 1px solid #000;
     box-shadow: 0 1px 0 0 #000;
   }
   .input-field .prefix.active {
     color: #000;
   }

</style>



