import Promise from 'bluebird'
import _ from 'lodash'


export default class PokemonService{

    baseUrl = 'v2/pokemon{/id}'
    pokemonSprite = 'https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/'

    constructor(resource){
        this._resource = resource
    }


    listPokemons(offset, limit) {

          return this._resource(`${this.baseUrl}?offset=${offset}&limit=${limit}`)
            .query()
            .then(res => res.json())
            .then(pokemons => { 
                console.log(pokemons)

               return(
                    _.map(pokemons.results, (pokemon, i)=>{
                    const id =  i + offset + 1;
                  
                   
                    return  {
                        name: pokemon.name,
                        image: `${this.pokemonSprite}${id}.png`,
                        id
                    } 

                   
                })
               )
            })
                  
            
    }


    getPokemonById(id) {


        const aboutThisPokemon = (pokemon) =>{
            return new Promise((resolve, reject) =>{
               if(pokemon){
                    resolve({
                        id:pokemon.id,
                        name:pokemon.name,
                        stats:pokemon.stats,
                        weight:pokemon.weight,
                        image:pokemon.sprites.front_default
                    })
               }
            })
        }
        
        return this._resource(`${this.baseUrl}`)
            .get({ id })
            .then(res => res.json())
            .then(pokemon => aboutThisPokemon(pokemon))


    }


}


